<template>
  <div class="testone-class">
    <img :src="'data:image/png;base64,'+imgurl" alt="">
    <van-button type="primary" size="large" round @click="handlerPush">下一页</van-button>
    <van-button type="info" size="large" round @click="handlerBack">上一页</van-button>
    <van-button
      type="danger"
      size="large"
      round
      color="#7232dd"
      @click="handlerOpenMicroApp"
    >跳转其他微应用</van-button>
    <van-button type="danger" size="large" round color="#322144" @click="handlerOpenBaidu">跳转http 百度</van-button>
    <van-button size="large" round color="#dda0dd" @click="handlerOpenYoutube">跳转https youtube 需翻墙</van-button>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  mounted() {
    console.log("testOnePage-->mounted")
  },
  methods: {
    onNativeShow() {
      console.log('textone-page');
      console.log('textone-page');
      console.log('textone-page');
      console.log('textone-page');
      console.log('textone-page');
      console.log('textone-page');
    },
    handlerPush() {
      this.$router.push({
        path: "/testtwo",
        query: {
          id: 1,
          age: 18,
          name: "中文",
          changeNavTitle: "haha",
        },
      })
    },
    handlerBack() {
      this.$router.go(-1)
    },

    // scheme tip:
    // file
    // omp
    // http
    // https
    // microapp
    handlerOpenMicroApp() {
      this.$engine.api(
        "com.zkty.jsi.direct",
        "push",
        {
          scheme: "microapp",
          host: "com.gm.microapp.home",
          pathname: "",
          fragment: "",
          params: {
            hideNavbar: true,
          },
        },
        function (res) {
          console.log("res :>> ", res)
        }
      )
    },
    handlerOpenBaidu() {
      this.$engine.api(
        "com.zkty.jsi.direct",
        "push",
        {
          scheme: "http",
          host: "www.baidu.com",
          pathname: "",
          fragment: "",
          params: {
            hideNavbar: true,
          },
        },
        function (res) {
          console.log("res :>> ", res)
        }
      )
    },
    // https
    handlerOpenYoutube() {
      this.$engine.api(
        "com.zkty.jsi.direct",
        "push",
        {
          scheme: "https",
          host: "www.youtube.com",
          pathname: "",
          fragment: "",
          params: {
            hideNavbar: true,
          },
        },
        function (res) {
          console.log("res :>> ", res)
        }
      )
    },
    // 跳转microapp的某个页面
    // host: 跳转的microapp名称
    // fragment: 跳转的页面名称
    handlerToOtherMicroapp() {
      this.$engine.api(
        "com.zkty.jsi.direct",
        "push",
        {
          scheme: "microapp",
          host: "com.gm.microapp.xxx",
          pathname: "/",
          fragment: "/xxx",
        },
        function (res) {
          console.log("res :>> ", res)
        }
      )
    },

    // 跳转microapp的某个页面并且携带参数, 在下一个页面通过this.$router.query.xx来获取传递参数
    // host: 跳转的microapp名称
    // fragment: 跳转的页面名称
    // query: 跳转携带的参数
    handlerToOtherMicroappAndQuery() {
      this.$engine.api(
        "com.zkty.jsi.direct",
        "push",
        {
          scheme: "microapp",
          host: "com.gm.microapp.xxx",
          pathname: "/",
          fragment: "/xxx",
          query: {
            id: 1,
            name: "x-engine",
          },
        },
        function (res) {
          console.log("res :>> ", res)
        }
      )
    },
  },
}
</script>

<style scoped>
.testone-class {
  margin: 0 20px;
}
.van-button {
  margin-top: 10px;
}
</style>
